import React, { Component } from "react";
import {
  CButton,
  CFormGroup,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CLabel,
  CInput,
  CInputFile,
} from "@coreui/react";
import api from "../../api";
import authHeader from "../../auth-header";
import authHeaderFile from "../../auth-header-file";
import Toast from "light-toast";
// import swal from "sweetalert";
import { ScaleLoader } from "react-spinners";
import { css } from "@emotion/core";
import Select from "react-select";

const override = css`
  display: block;
  border-color: black;
  position: absolute;
  left: 43%;
  top: 50%;
  z-index: 1;
`;

class EditAdvertisement extends Component {
  constructor(props) {
    super(props);

    this.handleChange = this.handleChange.bind(this);
    this.onChangeFile = this.onChangeFile.bind(this);
    this.adSubmit = this.adSubmit.bind(this);
    this.cancel = this.cancel.bind(this);
    this.state = {
      loading: false,
      id: null,
      imageChanged: false,
      adImage: null,
      adImagePreview: null,
      adName: null,
      shopId: null,

      adImageError: null,
      adNameError: null,
      shopIdError: null,

      options: [],
    };
  }

  componentDidMount() {
    var data = localStorage.getItem("ad_id");
    this.setState({ id: data });
    var con = {
      id: data,
    };
    this.setState({ loading: true });
    this.getServiceProviders();
    fetch(api.apiUrl + "fetch_ad_data", {
      method: "POST",
      headers: authHeader(),
      body: JSON.stringify(con),
    })
      .then((response) => response.json())
      .then((responseJsonFromServerData) => {
        // console.log(responseJsonFromServerData);
        if (responseJsonFromServerData !== null) {
          this.setState({
            adImagePreview: api.imgUrlAds + responseJsonFromServerData.adImage,
            adName: responseJsonFromServerData.adName,
            shopId: responseJsonFromServerData.shopId,
          });
        } else {
          Toast.fail("failed...", 600, () => {
            this.props.history.push("/advertisement-list");
          });
        }
        this.setState({ loading: false });
      })
      .catch((error) => {
        console.error(error);
        this.setState({ loading: false });
      });
  }

  getServiceProviders = () => {
    this.setState({ loading: true });
    fetch(api.apiUrl + "get_service_providers", {
      method: "GET",
      headers: authHeaderFile(),
      // body: JSON.stringify(data),
    })
      .then((response) => response.json())
      .then((responseJsonFromServerData) => {
        console.log(responseJsonFromServerData);

        try {
          this.setState({
            options: [
              { value: "null", label: "None" },
              ...responseJsonFromServerData.options,
            ],
          });
        } catch (err) {
          console.log(err);
        }
        this.setState({ loading: false });
      })
      .catch((error) => {
        console.error(error);
        this.setState({ loading: false });
      });
  };

  handleChange = (input) => (e) => {
    // console.log(input);
    // console.log(e.target.value);
    this.setState({ [input]: e.target.value });
    this.setState({ [input + "Error"]: "" });
  };

  onChangeFile = (event) => {
    const imageFile = event.target.files[0];
    var stat = false;
    if (!imageFile) {
      console.log("1");
      this.setState({ adImageError: "Please select image", adImage: null });
      stat = true;
      //   return false;
    }
    // console.log(imageFile.name);
    if (!imageFile.name.match(/\.(jpg|jpeg|png)$/)) {
      console.log("2");
      this.setState({
        adImageError: "Please select valid image",
        adImage: null,
      });
      stat = true;
      //   return false;
    }

    if (Math.round(imageFile.size / 1024) > 500) {
      console.log("3");
      this.setState({ adImageError: "Max file size 500kb", adImage: null });
      stat = true;
      //   return false;
    }

    if (stat === false) {
      this.setState({
        imageChanged: true,
        adImage: event.target.files[0],
        adImagePreview: URL.createObjectURL(event.target.files[0]),
        adImageError: "",
      });
    } else {
      console.log("failed");
      this.setState({
        adImage: null,
        adImagePreview: null,
      });
    }
  };

  adSubmit = () => {
    console.log("called");
    let error = false;
    if (this.state.adImage == null && this.state.imageChanged == true) {
      this.setState({ adImageError: "Select Advertisement Image" });
      error = true;
    } else {
      this.setState({ adImageError: null });
    }

    if (this.state.adName == null || this.state.adName == "") {
      this.setState({ adNameError: "Enter advertisement Name" });
      error = true;
    } else {
      this.setState({ adNameError: null });
    }

    // if (this.state.shopId == null) {
    //   this.setState({ shopIdError: "Service Provider not selected" });
    //   error = true;
    // } else {
    //   this.setState({ shopIdError: null });
    // }

    if (error == false) {
      var formData = new FormData();

      formData.append("adImage", this.state.adImage);
      formData.append("id", this.state.id);
      formData.append("imageChanged", this.state.imageChanged);
      formData.append("adName", this.state.adName);

      formData.append("shopId", this.state.shopId);

      //   formData.append("createdAt", new Date());
      //   formData.append("updatedAt", new Date());

      this.setState({ loading: true });
      fetch(api.apiUrl + "update_advertisement", {
        method: "POST",
        headers: authHeaderFile(),
        // body: JSON.stringify(data),
        body: formData,
      })
        .then((response) => response.json())
        .then((responseJsonFromServerData) => {
          // console.log(responseJsonFromServerData);
          if (responseJsonFromServerData.error == false) {
            //   console.log("sucess1");
            Toast.success("Updated successfully...", 600, () => {
              // do something after the toast disappears
              document.getElementById("adNameForm").reset();
              this.setState({
                adImage: null,
                adImagePreview: null,
                adName: null,
                shopId: null,
                id: null,
                imageChanged: false,
                adImageError: null,
                adNameError: null,
                shopIdError: null,
              });
              localStorage.removeItem("ad_id");
              this.props.history.push({
                pathname: "/advertisement-list",
                data: 0,
              });
            });
          } else if (responseJsonFromServerData.error == true) {
            Toast.fail("failed...", 600, () => {
              // do something after the toast disappears
            });
          } else if (responseJsonFromServerData.error === "format_invalid") {
            Toast.fail("Selected Image is of invalid format...", 600, () => {
              // do something after the toast disappears
            });
          }
          this.setState({ loading: false });
        })
        .catch((error) => {
          console.error(error);
          this.setState({ loading: false });
        });
    }
  };

  cancel = () => {
    document.getElementById("adNameForm").reset();
    this.props.history.push("/advertisement-list");
  };

  render() {
    return (
      <>
        <CRow>
          <CCol>
            <CCard>
              <ScaleLoader
                height={40}
                width={12}
                css={override}
                size={150}
                //size={"150px"} this also works
                color={"#B54545"}
                loading={this.state.loading}
              />
              <CCardHeader>
                {" "}
                <CRow>
                  <CCol sm="6" xs="6" md="8">
                    <h3>Edit Advertisement</h3>
                  </CCol>
                  <CCol sm="6" xs="6" md="4"></CCol>
                </CRow>
              </CCardHeader>
              <CCardBody>
                <CRow>
                  <CCol md="3" sm="3" xs="1"></CCol>
                  <CCol md="6" sm="6" xs="10">
                    <form id="adNameForm">
                      <CFormGroup>
                        <CLabel htmlFor="">Advertisement</CLabel>
                        <CInputFile
                          id=""
                          name=""
                          onChange={this.onChangeFile}
                        />
                      </CFormGroup>
                      <div
                        style={{
                          marginBottom: "10px",
                          display:
                            this.state.adImagePreview !== null
                              ? "block"
                              : "none",
                        }}
                      >
                        <img
                          style={{ maxWidth: "327px", maxHeight: "125px" }}
                          src={this.state.adImagePreview}
                          alt="Advertisement image"
                        />
                      </div>

                      <p
                        className="error"
                        style={{
                          display:
                            this.state.adImageError === null ? "none" : "block",
                        }}
                      >
                        <i className="fa fa-exclamation-triangle"></i>{" "}
                        {this.state.adImageError}
                      </p>

                      <CFormGroup>
                        <CLabel htmlFor="adName">Advertisement Name</CLabel>

                        <CInput
                          autoFocus
                          type="text"
                          placeholder="Enter Advertisement Name"
                          value={this.state.adName}
                          onChange={this.handleChange("adName")}
                          style={{
                            borderColor:
                              this.state.adNameError === null ? "" : "red",
                          }}
                        />
                      </CFormGroup>
                      <p
                        className="error"
                        style={{
                          display:
                            this.state.adNameError === null ? "none" : "block",
                        }}
                      >
                        <i className="fa fa-exclamation-triangle"></i>{" "}
                        {this.state.adNameError}
                      </p>

                      <CFormGroup>
                        <CLabel htmlFor="shopId">
                          Select Service Provider
                        </CLabel>

                        <Select
                          options={this.state.options}
                          isClearable={true}
                          value={this.state.options.filter((item) => {
                            return item.value == this.state.shopId;
                          })}
                          inputId={this.state.shopId}
                          onChange={(val) => {
                            // alert(val.value)
                            if (val != null) {
                              this.setState({ shopId: val.value });
                            } else {
                              this.setState({ shopId: null });
                            }
                            console.log(val);
                          }}
                        />
                      </CFormGroup>
                      <p
                        className="error"
                        style={{
                          display:
                            this.state.shopIdError === null ? "none" : "block",
                        }}
                      >
                        <i className="fa fa-exclamation-triangle"></i>{" "}
                        {this.state.shopIdError}
                      </p>

                      <CCol md="12" sm="12" xs="12" className="text-center">
                        <CButton
                          className="px-4 cancel-btn"
                          onClick={() => this.cancel()}
                        >
                          Cancel
                        </CButton>
                        <CButton
                          className="px-4 submit-btn"
                          onClick={this.adSubmit}
                        >
                          Submit
                        </CButton>
                      </CCol>
                    </form>
                  </CCol>
                </CRow>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </>
    );
  }
}

export default EditAdvertisement;
